.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
			&::before {
				left: 50%;
				top: 0;
				width: 2px;
				height: 25px;
				content: "";
				position: absolute;
				background: linear-gradient(to bottom, #c6a058 0%, #ffda92 100%);
				border-radius: 50px;
				transform: translateY(-100%);
			}
			&::after {
				left: 50%;
				bottom: 0;
				width: 2px;
				height: 25px;
				content: "";
				position: absolute;
				background: linear-gradient(to bottom, #c6a058 0%, #ffda92 100%);
				border-radius: 50px;
				transform: translateY(100%);
			}
		}
		margin: 1rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.orn-2 {
		position: absolute;
		left: 0;
		top: 0;
		width: 130px;
		@include max-md {
			width: 100px;
			height: auto;
			transform: translateY(6rem);
		}
	}
	.orn-3 {
		position: absolute;
		right: 0;
		top: 0;
		width: 140px;
		@include max-md {
			width: 100px;
			height: auto;
			transform: translateY(-6rem);
		}
	}
	.couple {
		position: relative;
		.image-wrap {
			width: 80%;
			margin: auto;
			position: relative;
			margin-bottom: 1.25rem;
			aspect-ratio: 4/5;
			.orn-1 {
				position: absolute;
				top: 0;
				left: 0;
				transform: translate(-40%, 0);
				height: 100%;
				width: auto;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				mask-image: url(../images/substract-couple.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
			}
			.border-ribbon {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: calc(100% - 20px);
				height: calc(100% - 20px);
			}
		}
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
