.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	background-color: var(--color-primary);
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: auto;
		height: 100%;
		@include max-lg {
			height: 50%;
			top: unset;
			bottom: 0;
		}
	}
	.image-wrapper {
		position: absolute;
		inset: 0;
		width: 75%;
		@include max-lg {
			width: 100%;
			height: 70%;
		}
		&::after {
			position: absolute;
			inset: 0;
			background: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: relative;
		}
	}
	.col-h {
		height: 100%;
		@include max-lg {
			height: fit-content;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 4rem 0 3rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		@include max-lg {
			padding: 4rem 0 5rem;
		}
		.header {
			text-align: center;
			width: 100%;
			height: 80%;
			position: relative;
			background-color: var(--color-main);
			mask-image: url(../images/substract-header.png);
			mask-size: 100% 100%;
			mask-position: center;
			mask-repeat: no-repeat;
			@include max-lg {
				height: fit-content;
				padding: 5rem 0;
			}
			.border-substract {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: calc(100% - 20px);
				height: calc(100% - 20px);
			}
			.header-title {
				position: relative;
				height: 100%;
				width: 100%;
				color: var(--color-dark);
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				.title {
					position: relative;
					@include min-lg {
						margin-bottom: 2.5rem !important;
						&::after {
							left: 50%;
							bottom: 0;
							width: 2px;
							height: 2rem;
							content: "";
							position: absolute;
							background: linear-gradient(to bottom, #c6a058 0%, #ffda92 100%);
							border-radius: 50px;
							transform: translateY(100%);
						}
					}
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.4);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		padding: 4rem 0 5rem;
		@include max-md {
			padding: 4rem 0 8rem;
		}
		.header {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			background-color: transparent;
			height: 100%;
			width: 100%;
			@include max-md {
				padding: 0 0 3rem;
			}
			.header-title {
				height: fit-content;
				color: var(--xolor-light);
				@include max-md {
					padding: 3rem 0;
				}
				.title {
					margin-bottom: 1.5rem !important;
					&::after {
						all: unset;
					}
				}
			}
		}
	}
}
