.wishes-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: absolute;
		inset: 0;
		&::before {
			position: absolute;
			inset: 0;
			content: "";
			background-color: rgba($color: #000000, $alpha: 0.35);
			z-index: 1;
		}
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.card-form {
		margin-bottom: 0;
		background-color: var(--color-main);
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			@include min-md {
				padding: 2rem;
			}
			@include max-md {
				padding: 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		color: var(--color-light);
		z-index: 3;
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				.wishes {
					text-align: left;
					height: 25.5rem;
					overflow: auto;
					padding-right: 0.5rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background: #888;
						border-radius: 2px;
					}
					&::-webkit-scrollbar-thumb {
						background: var(--color-secondary);
						border-radius: 2px;
					}
					.text-muted {
						color: rgba($color: #fff, $alpha: 0.8) !important;
					}
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
