.stream-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	@include max-lg {
		.row {
			gap: 1.5rem;
		}
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 180px;
		transform: translate(-40%, -30%);
		@include max-md {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 8rem;
		right: 0;
		transform: translateX(40%);
		width: 240px;
	}
	.stream-wrapper {
		position: relative;
		background-color: var(--color-main);
		padding: 3rem 2rem;
		border-radius: 10px;
		height: 100%;
		mask-image: url(../images/substract-header.png);
		mask-position: center;
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		z-index: 3;
		@include max-md {
			padding: 2rem 1rem;
		}
		h4 {
			@include max-md {
				text-align: center;
			}
		}
		.border-subtract {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% - 20px);
			height: calc(100% - 20px);
		}
	}
	.card {
		background-color: var(--color-primary);
		color: var(--color-light);
		margin-left: -5rem;
		@include max-lg {
			margin-left: 0;
			margin-top: -5rem;
		}
		.card-body {
			padding: 2rem 2rem 2rem 5rem;
			@include max-lg {
				padding: 5rem 2rem 2rem;
			}
			@include max-md {
				padding: 5rem 1rem 1rem;
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 50%;
		@include max-lg {
			width: 100%;
		}
		@include max-md {
			width: fit-content;
		}
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
