.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.row {
			gap: 3rem;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		margin-top: -8rem;
		padding: 3rem 2rem;
		border-radius: 8px;
		background-color: var(--color-main);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 19/7;
		@include max-lg {
			aspect-ratio: 19/10;
		}
		@include max-md {
			aspect-ratio: 3/4;
		}
		&::before {
			position: absolute;
			inset: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
