.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	background-color: var(--color-primary);
	@include max-lg {
		padding: 20rem 0 5rem;
	}
	@include max-md {
		padding: 20rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: auto;
		height: 100%;
		z-index: 3;
		@include max-lg {
			height: 50%;
			bottom: 0;
			top: unset;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 75%;
		height: 100%;
		@include max-lg {
			width: 100%;
			height: 70%;
		}
		&::before {
			position: absolute;
			inset: 0;
			background-color: rgba($color: #000000, $alpha: 0.4);
			content: "";
			z-index: 2;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.thank {
		position: relative;
		padding: 3rem 1.5rem;
		background-color: var(--color-main);
		color: var(--color-dark);
		mask-image: url(../images/substract-header.png);
		mask-size: 100% 100%;
		mask-position: center;
		mask-repeat: no-repeat;
		z-index: 5;
		@include max-md {
			padding: 3rem 1.5rem;
			width: 100%;
		}
		.border-subtract {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% - 20px);
			height: calc(100% - 20px);
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	small {
		position: relative;
	}
}
