.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-40%, -30%);
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(40%, 30%) scaleX(-1);
	}
	.appology-wrap {
		position: relative;
		border-radius: 8px;
		background-color: var(--color-main);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
