.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 30%;
		width: 120px;
	}
	.orn-2 {
		position: absolute;
		right: 0;
		bottom: 0;
		transform: scaleX(-1);
		width: 120px;
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			.card {
				border: none;
				height: 100%;
				background-color: transparent;
				border-radius: 10px;
				color: var(--color-light);
				filter: drop-shadow(0 0.5rem 1rem rgba(0, 0, 0, 0.25));
				.substract-border {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: calc(100% - 20px);
					height: calc(100% - 20px);
				}
				.card-body {
					padding: 3rem 2rem;
					background-color: var(--color-primary);
					position: relative;
					mask-image: url(../images/substract-venue.png);
					mask-size: 100% 100%;
					mask-position: center;
					mask-repeat: no-repeat;
					@include max-md {
						padding: 2rem 1rem;
					}
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		h6 {
			font-weight: 600px;
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-button-trans);
		color: var(--color-button-trans);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary) !important;
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
}

.maps-embed {
	position: absolute;
	width: 100%;
	height: 100%;
	inset: 0;
}
